body {
    background-color: #555 !important;
    background-image: radial-gradient( #666, #455);
    -webkit-box-shadow: 14px 10px 413px 3px rgba(0, 0, 0, 0.6);
    -moz-box-shadow: 14px 10px 43px 3px rgba(0, 0, 0, 0.6);
    box-shadow: 14px 10px 43px 3px rgba(0, 0, 0, 0.6);
}

.App {
    text-align: center;
    color: #ccc;
}

.App .rc-slider-handle {
    border: solid 2px #ff5555;
    background-color: #fff;
}

.App .rc-slider-handle:hover {
    border-color: #ffaaaa;
}

.App .rc-slider-track {
    background-color: #ff5555;
}

.App .app-container {
    margin: 0 0 !important;
}

.App .pane {
    background-color: #555;
    /* border: 1px solid #faa; */
    background-image: linear-gradient( #777, #555);
    -webkit-box-shadow: 14px 10px 413px 3px rgba(0, 0, 0, 0.6);
    -moz-box-shadow: 14px 10px 43px 3px rgba(0, 0, 0, 0.6);
    box-shadow: 14px 10px 43px 3px rgba(0, 0, 0, 0.6);
}

.App .container {
    /* padding: 0px; */
    margin: 10px 0;
    padding-bottom: 10px;
    border-radius: 5px;
    /* padding-top: 10px; */
}
 

.App .pane .pane-title {
    text-transform: uppercase;
    padding: 0 1rem;
    margin-bottom: 0.5rem;
    background-color: #f55;
    text-align: left;
    border-radius: 5px 5px 0 0;
}

.App .pane-title {
    text-transform: uppercase;
    padding: 0 1rem;
    margin-bottom: 0.5rem;
    background-color: #f55;
    text-align: left;
    border-radius: 5px 5px 0 0;
}

.App .pane-body {
    padding: 0 1rem;
    display: block;
}

.Planner {
    font-size: 80%;
    /* background-color: #777; */
}

.Planner .locked {
    background-color: #6c757d;
}

.Planner .step {
    cursor: pointer;
    background-color: #6c757d40;
    margin-bottom: 1px;
}

.Planner .current-step {
    background-color: #f55;
}

.SimpleProgress .bar {
    padding: 1px;
    background-color: #f55;
}

.ModePanel input {
    margin: 0 5px;
    /* width: 4rem; */
    display: inline;
}

.ModePanel .pane-body div {
    margin: 5px 0;
}

.SimpleVis {
    position: relative;
}

.SimpleVis div {
    /* width: 2rem; */
    width: 2rem;
    height: 2rem;
    line-height: 2rem;
    font-size: 1rem;
    text-align: center;
    /* border: 3px solid; */
    border-radius: 50%;
    /* background-color: #f55; */
}

.SimpleVis .active {
    /* border: 3px solid #f55; */
    background-color: #f55;
    color: #555;
    /* text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e60073, 0 0 40px #e60073, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073; */
}

.PresetsManager .preset {
    font-size: 0.8rem;
    cursor: pointer;
    box-shadow: 1px 0 0 0 #888, 0 1px 0 0 #888, 1px 1px 0 0 #888, 1px 0 0 0 #888 inset, 0 1px 0 0 #888 inset;
}

.PresetsManager .preset .x {
    position: absolute;
    right: 0;
    top: 0;
    font-family: Arial, Helvetica, sans-serif;
    padding-right: 1px;
    color: #555;
    font-weight: 300;
    
    
    /* visibility: hidden; */
    background-color: #f55;
}